.nav__link {
  box-sizing: border-box;
  height: 34px;
  letter-spacing: 0;
  text-align: center;
  cursor: pointer;
  color: #000;
  text-transform: uppercase;
  background-color: #fff;
  border: 0;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  padding: 10px 26px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  display: flex;
}

.nav__link:hover, .nav__link:focus, .nav__link--current {
  color: #fff;
  background-color: #000;
}

.nav__link--current:before, .nav__link--current:after {
  content: "";
  z-index: -1;
  border-radius: 100px;
  margin: -2%;
  animation: anim-nav-link 6s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 0 3px #075e98;
}

.nav__link--current:before {
  animation-delay: -4s;
}

.nav__link--current:hover:after, .nav__link--current:hover:before {
  background-color: #1a1919;
}

@keyframes anim-nav-link {
  0%, 100% {
    clip: rect(0px, 220px, 6px, 0px);
  }

  25% {
    clip: rect(0px, 6px, 220px, 0px);
  }

  50% {
    clip: rect(218px, 220px, 220px, 0px);
  }

  75% {
    clip: rect(0px, 220px, 220px, 218px);
  }
}

